<template>
  <div id="listActs">
    <h1>List All Acts</h1>
    <div class="list-group">
      <router-link 
        v-for="act in acts" 
        v-bind:key="act.id" 
        v-bind:to="act.id" 
        class="list-group-item list-group-item-action"
        >
        <div class="badge badge-secondary">
          {{ act.createdLocal }}
        </div>
        <div v-if="act.updatedAt" class="badge badge-primary">
          updated: {{ act.updatedLocal }}
        </div>
        {{ act.name }}
      </router-link>
    </div>
  </div>
</template>
<script>
import db from '../lib/firebaseInit'
export default {
  name:'listActs'
  , data() {
    return {
      updatedAt:''
      , createdAt: ''
      , acts: []
    }
  }, 
  created() {
    db.collection('acts')
    .orderBy('act_name')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = doc.data()
        const act = {
          'id':doc.id
          , 'name': data.act_name
        }
        act.createdLocal = new Date( data.createdAt ).toDateString() 
        act.updatedLocal = data.updatedAt 
          ? new Date( data.updatedAt ).toLocaleString("en-GB") 
          : null
        this.acts.push( act )
      } ) 
    } )
  }
}
</script>
